<template>
    <v-container>
        <v-layout>
            <v-flex xs12 md8 offset-md2 class="mt-5 pt-5">
                <v-card>
                    <v-card-title
                        class="headline indigo white--text"
                        primary-title
                    >
                        Want to create a new account?
                    </v-card-title>
                        <v-form
                        @submit.prevent="signUp"
                        class="pa-4"
                        ref="form"
                        v-model="valid"
                        lazy-validation
                    >
                        <v-text-field
                        v-model="firstName"
                        :rules="firstNameRules"
                        label="First Name"
                        required
                        ></v-text-field>

                        <v-text-field
                        v-model="lastName"
                        :rules="lastNameRules"
                        label="Last Name"
                        required
                        ></v-text-field>

                        <v-text-field
                        v-model="email"
                        :rules="emailRules"
                        label="E-mail"
                        required
                        ></v-text-field>

                        <v-text-field
                        label="Password"
                        v-model="password"
                        required
                        :rules="passwordRules"
                        :type="'password'"
                        ></v-text-field>

                        <v-text-field
                        label="Re-type Password"
                        v-model="repeatPassword"
                        :error-messages='passwordMatchError()'
                        required
                        :type="'password'"
                        ></v-text-field>

                        <v-text-field
                        v-model="aboutMe"
                        label="About Me"
                        ></v-text-field>
                        <p v-if="error" class="red--text"> {{ error }} </p>
                        <v-btn
                        type="submit"
                        :disabled="!valid"
                        class="indigo white--text text-capitalize mt-4"
                        >
                        Create a new account
                        </v-btn>
                    </v-form>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import db from "@/firebase/init"
import firebase from "firebase"
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

  export default {
    data: () => ({
      valid: true,
      firstName: '',
      firstNameRules: [
        v => !!v || 'First name is required',
        v => (v && v.length <= 20) || 'Name must be less than 20 characters'
      ],
      lastName: '',
      lastNameRules: [
        v => !!v || 'Last name is required',
        v => (v && v.length <= 20) || 'Name must be less than 20 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      password: '',
      repeatPassword: '',
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 6) || 'Password must be longer than 6 characters'
      ],
      aboutMe: '',
      error: null
    }),

    methods: {
      signUp () {
        NProgress.start()
        if (this.$refs.form.validate()) {
          
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(cred => {
              let user_id = cred.user.uid
              db.collection('users').doc(cred.user.uid).set({
                id: user_id,
                firstName: this.firstName,
                lastName: this.lastName,
                aboutMe: this.aboutMe
              });
            }).then(() => {
              NProgress.done()
              this.$router.push({ name: 'users' })
            })
            .catch(err => this.error = err);
        }
      },
      passwordMatchError () {
          return (this.password === this.repeatPassword) ? '' : 'Passwords do not match'
      }
    }
  }
</script>